import { Dialog } from '@radix-ui/themes';
import { useState } from 'react';
import logo from '../../../asset/image 3.png';
import './FirstModal.css';
import ModalInputs from './ModalInputs';
import header from '../../../asset/ModalHeader.png';
import modalMonkey from '../../../asset/modalMonkey.png';
import BlueButton from './BlueButton';

export function FirstModal({
  isOpen,
  handleCheck,
  handleChangeModal,
  postMailHelper,
}) {
  const [step, setStep] = useState(1);

  const postMailHelperClick = () => {
    if (typeof postMailHelper === 'function') {
      postMailHelper();
    } else {
      console.log(typeof postMailHelper);
    }
  };

  const renderContent = () => {
    switch (step) {
      case 1:
        return (
          <div style={{ position: 'relative' }}>
            {/* <Dialog.Close>
              <Button style={{ background: 'none', color: 'black ' }}>X</Button>
            </Dialog.Close> */}
            <img
              src={header}
              alt=""
              style={{ width: '100%', height: '40px', margin: '0 auto' }}
            />
            <div>
              <img
                className="logo"
                src={logo}
                alt=""
                style={{
                  width: '155px',
                  height: '39px',
                  marginTop: '30px',
                  marginLeft: '165px',
                }}
              />
            </div>
            <p className="firstment">
                ,
              <br />
              ,    . <br />
                  ,  !
            </p>
            <p className="secondment">
                  
              <br />
              
            </p>
            <BlueButton
              className="next"
              style={{ marginRight: '110px', marginTop: '21px' }}
              onClick={() => {
                setStep(2);
              }}
            >
              AI 
            </BlueButton>

            <img
              src={modalMonkey}
              alt="  "
              style={{
                position: 'absolute',
                bottom: '0',
                left: '100px',
                top: '230px',
                opacity: 0.5,
                pointerEvents: 'none',
              }}
            />
          </div>
        );
      case 2:
        return (
          <div>
            <img
              src={header}
              alt=""
              style={{ width: '100%', height: '40px', margin: '0 auto' }}
            />
            <ModalInputs />
            <div style={{ position: 'relative' }}>
              <BlueButton
                style={{ marginRight: '110px', marginTop: '21px' }}
                onClick={() => {
                  handleCheck();
                  postMailHelperClick();
                }}
              >
                
              </BlueButton>
              <img
                src={modalMonkey}
                alt="  "
                style={{
                  position: 'absolute',
                  bottom: '0',
                  left: '100px',
                  opacity: 0.5,
                  pointerEvents: 'none',
                }}
              />
            </div>
          </div>
        );
      default:
        return null;
    }
  };

  return (
    <Dialog.Root open={isOpen} onOpenChange={handleChangeModal}>
      <Dialog.Content style={{ maxWidth: 470, height: 608, padding: '0px' }}>
        {renderContent()}
      </Dialog.Content>
    </Dialog.Root>
  );
}
