import './SelectBox.css';
const Options = [
  { id: 0, value: '' },
  { id: 1, value: '' },
  { id: 2, value: '' },
  { id: 3, value: '' },
  { id: 4, value: '' },
  { id: 5, value: '' },
];

function SelectBox({ onChange, name }) {
  const options = Options;
  return (
    <select className="select-box" name={name} onChange={onChange}>
      {options.map((option) => (
        <option key={option.id} value={option.value}>
          {option.value}
        </option>
      ))}
    </select>
  );
}

export default SelectBox;
